#footer{
    background-color: var(--background-color);
    width: 100%;
    height: 120px;
    display: flex;
    align-items: center;
    position: relative;
    border-bottom: solid 5px var(--accent-color);
}
#footer > h2{
    margin-left: 50px;
    font-size: 30px;
}
#footer-socials{
    position: absolute;
    right: 50px;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 20px;
}

@media screen and (max-device-width: 1024px){
    #footer{
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    #footer > h2{
        position: relative;
        left: 0;
        margin: 0;
    }
    #footer-socials{
        position: relative;
        right: 0;
        margin-top: 10px;
    }
}